.edu-heading {
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.edu-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.edu-card {
    background: #1a1919;
    padding: 1.2rem 1rem;
}

@media screen and (max-width: 740px) {
    .edu-container {
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
    .edu-title {
        text-align: center;
    }
}

.edu-title {
    padding: 50px 100px;
}

.edu-btn {
    padding: 12px 100px;
    font-size: 1rem;
    text-transform: uppercase;
    background: rgb(26, 100, 197);
    color: black;
    border: 1px solid #fff;
    font-weight: 600;
    cursor: pointer;
    border-radius: 30px;
}

.edu-btn:hover {
    background: rgba(255, 255, 255, 0.2);
    color: #fff;
    transition: 0.3s;
  }
  